import styled,{css} from 'styled-components';

export const Container = styled.div`
  
    
   height:100vh;
   width:100%;
   margin-top:5px;
   position:relative;
   background:white;
`;

export const Triangule = styled.div`

   
width: 0;
height: 0;
border-style: solid;
border-width: 600px 0 0 98.5vw;
border-color: transparent transparent transparent #350455;
position:absolute;
bottom:0;
`;

export const SectionName = styled.div`


background:#350455;
 width:50%;
margin-top:13px;

>h1{

  
   
    color:white;

  font-size:55px;
  padding:20px;
  font-family: 'Montserrat', sans-serif;
}


`;



export const Content = styled.div`
   display:flex;
   flex-direction:row;
   position:absolute;
    right:10%;
   top:25%;
   
   
`;

export const Projetos = styled.div`
  margin-left:70px;

`;



export const Listras = styled.div`
    position:absolute;
    display:flex;
    flex-direction:row;

    bottom:0;
    right:0;

`;

const listraCss = css`

   height:800px;
   width:30px;
   background:#350455;
   margin-right:20px;

`;

export const Listra1 = styled.div`${listraCss}`;
export const Listra2 = styled.div`${listraCss}`;

